import Vue from 'vue';
import IconComponent from './IconComponent.vue';
export default Vue.extend({
    name: 'PopupWindow',
    components: { IconComponent },
    props: {
        /**
         * Список элементов popup
         * @type {Array<any>}
         */
        action: Array,
        /**
         * Данные вызывающего popup элемента
         * @type {Object}
         */
        target: Object
    },
    data() {
        return {
            /**
            * Состояние popup
            * @type {boolean}
            */
            isOpen: false
        };
    },
    methods: {
        /**
           * Обработчик нажатия на элемент
           * @param {any} action
           */
        Action(action) {
            console.log(action);
            this.isOpen = false;
        }
    }
});
